<template>
  <div :style="randomColor">hello</div>
  <button @click="showStatistiks" >click</button>
</template>

<script>
export default {
  name: 'test',
  props: {
    component: {
    },
  },
  mounted() {
    const a = this.component.$el;
    a.textContent = 'dada';
    console.log(this.component);
    console.log(this.component.$options.watch);
    console.log(this.component.$options.props);
    console.log(this.component.$refs.pipay);
    Object.keys(this.component.$refs).forEach((item) => console.log(item));
  },
  computed: {
    randomColor() {
      if (this.component.$store.getters['componentInfo/getComponentStatus']) {
        return {
          border: `dashed 1px  #${Math.floor(Math.random() * 16777215).toString(16)}`,
          'margin-top': '5px',
        };
      }
      return false;
    },
  },
  methods: {
    showStatistiks() {
      if (this.randomColor) {
        let arrayData = [];
        if (this.component.$options.data) {
          arrayData = Object.entries(this.component.$options.data());
        }
        arrayData = arrayData.map((item) => `${item[0]}:  ${item[1] ? item[1] : 'пустая строка'}`);
        console.log(this.component.$options.name);
        console.log(this.component.$options.components);
        console.log(this.component.$options.computed);
        console.log(this.component.$options.methods);
        console.log(arrayData);
        this.component.$store.dispatch('componentInfo/changeComponentStatistics', {
          name: this.component.$options.name,
          components: this.component.$options.components,
          computeds: this.component.$options.computed,
          methods: this.component.$options.methods,
          data: arrayData,
        });
      }
    },
  },
};
</script>
